import React, { Component } from 'react'
import Helmet from 'react-helmet'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/NavBar'
import Footer from '../components/Footer'
import PageTitle from '../components/PageTitle'
import PostRankTable, { PostRankData } from '../components/PostRankTable'
import axios from 'axios'
import searchService from '../service/SearchService'
import styled from 'styled-components'
import { TabWrapper, Tab } from '../components/PostRankTab'
import LoadingSpinner from '../components/LoadingSpinner'
import subDays from 'date-fns/sub_days'
import format from 'date-fns/format'

const TableWrap = styled.div`
  min-height: 70vh;
`

type PageView = {
  count: number,
  page: string,
}

type Props = {
  data: any,
}

type State = {
  postList: PostRankData[],
  pageViews: PageView[],
}

const WEEK = 'week'
const MONTH = 'month'
const YEAR = 'year'

class StatsRoute extends Component<Props, State> {
  tabs = [WEEK, MONTH, YEAR]
  tabNameMap = {
    [WEEK]: ' 7',
    [MONTH]: ' 30',
    [YEAR]: ' 1',
  }

  routeTitleMap = {}

  defaultTab = this.tabs[0]

  constructor(props) {
    super(props)
    this.state = {
      currentTab: this.defaultTab,
      pageViews: [],
      postList: [],
      isLoadingData: true,
    }
  }

  componentDidMount() {
    searchService.addMessageListener({
      onSearchWorkerReady: () => {
        if (searchService.isReady) {
          searchService.getRouteTitleMap()
        }
      },
      onReceiveRouteTitle: ({ routeTitleMap }) => {
        this.routeTitleMap = Object.assign({}, routeTitleMap)
        this.handleChangeTab(this.state.currentTab)
      },
    })

    //     search-worker   
    // onSearchWorkerReady   .
    if (searchService.isReady) {
      searchService.getRouteTitleMap()
    }
  }

  handleChangeTab = (tab = this.defaultTab) => {
    let startDate //    
    let endDate = subDays(new Date(), 1) //  
    const DATE_FORMAT = 'YYYY-MM-DD'

    this.setState({
      currentTab: tab,
      isLoadingData: true,
    })

    switch (tab) {
      case WEEK:
        startDate = subDays(endDate, 7)
        break
      case MONTH:
        startDate = subDays(endDate, 30)
        break
      case YEAR:
        startDate = subDays(endDate, 365)
        break

      default:
        break
    }

    axios
      .get(`https://blogapi.rhostem.ml/api/ga/post_pageviews`, {
        params: {
          startDate: format(startDate, DATE_FORMAT),
          endDate: format(endDate, DATE_FORMAT),
        },
      })
      .then(res => {
        const pageViews: PageView[] = res.data

        this.setState({
          isLoadingData: false,
          pageViews,
          postList: pageViews
            .map((pageView: PageView) => {
              if (this.routeTitleMap[pageView.page]) {
                return {
                  title: this.routeTitleMap[pageView.page],
                  route: pageView.page,
                  pageView: pageView.count,
                }
              } else {
                console.error('route not found for', pageView.page)
                return {}
              }
            })
            .filter(post => !!post.title),
        })
      })
  }

  render() {
    const siteMetadata = this.props.data.site.siteMetadata
    const title = ` `
    const pageTitle = `${title}  ${siteMetadata.title}`

    return (
      <PageWrapper>
        <Helmet
          title={pageTitle}
          description="Google analytics     "
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: pageTitle },
          ]}
        />
        <NavBar />
        <PostContentWrapper>
          <PageTitle>{title}</PageTitle>

          <TabWrapper>
            {this.tabs.map((tab, i) => (
              <Tab
                key={tab}
                onClick={() => this.handleChangeTab(tab)}
                isSelected={tab === this.state.currentTab}
              >
                {this.tabNameMap[tab]}
              </Tab>
            ))}
          </TabWrapper>

          <TableWrap>
            {this.state.isLoadingData && <LoadingSpinner />}
            <PostRankTable postList={this.state.postList} />
          </TableWrap>
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default StatsRoute

export const statsQuery = graphql`
  query statsQuery {
    site {
      siteMetadata {
        title
        url
      }
    }
  }
`



